<template>
	<view>
		<view class="header">
			<view class="search-w">
				<search-bar></search-bar>
			</view>
			<u-swiper 
				:list="list" 
				:interval="4000" 
				:height="300"
				mode="rect"
				border-radius="15"
				@click="handleNavigator"
			></u-swiper>
		</view>
		
		<view class="content" >
			<u-tabs 
				name="cate_name" 
				:show-bar="false"
				inactive-color="#999"
				:list="tabs" 
				:is-scroll="false" 
				:current="current"
				@change="tabsChange"
			></u-tabs>
				
			<view
				:style="{'display': current == i? 'block':'none'}"
				v-for="(item, i) in swiperData"
				:key="i"
			>	
					<qa-list
						:type="i"
						:end-flag="item.endFlag"
						:list="item.data"
						:loading="item.loading"
					></qa-list>

			</view>
		</view>
		<template v-if="!freshShow">
			<view class="add-btn" @click="handleSend">
				<u-icon name="plus" color="#fff" size="50"></u-icon>
				<view class="add-btn-text">
					{{infoAuthorize.type == 0 ? '提问' : '发声'}}
				</view>
				
			</view>
		</template>
		<template v-else>
			<view class="add-btn" @click="handleFresh">
				<u-icon name="reload" color="#fff" size="50"></u-icon>
				<view class="add-btn-text">刷新</view>
			</view>
		</template>
		
		<rz-select-modal
			:show="rzModalShow"
			@change-flag="handleShowRzBox"
		></rz-select-modal>
		
		<u-top-tips ref="uTips"></u-top-tips>
		<tab-bar></tab-bar>
		
	</view>
</template>

<script>
	import {sharePage} from '@/utils/sharePage.js'
	import {mapState} from 'vuex'
	import tabBar from '@/components/tabBar/tabBar.vue'
	import searchBar from '@/components/searchBar/searchBar.vue'
	import qaList from '@/components/QAList/QAList.vue'
	import rzSelectModal from '@/components/rzSelectModal/rzSelectModal.vue'
	
	export default {
		mixins: [sharePage],
		data() {
			return {
				current: 0,
				rzModalShow: false,
				loading: false,
				tabs: [
					{
						name: '记者提问',
					},
					{
						name: '专家发声'
					},
				],
				list: [
					{
						image: 'https://www.100ec.cn/zt/upload_data/19s11/images/tout.jpg',
						url: '/pages/search/search'
					},
					{
						image: 'https://www.100ec.cn/zt/upload_data/11_wfgl/images/banner3.jpg',
						url: '/pages/search/search'
					},
					{
						image: 'https://www.100ec.cn/zt/upload_data/19h5/images/top.jpg',
						url: '/pages/search/search'
					}
					
				],
				dataList: [],
				swiperData: [
					{
						data: [],
						api: 'questions_list',
						loading: false,
						p: 1,
						endFlag: false,
						scroll: false
					},
					{
						data: [],
						api: 'viewpoint_list',
						loading: false,
						p: 1,
						endFlag: false,
						scroll: false
					}
				],
				qData: [],
				vData: [],
				p: [1, 1],
				endFlag: [false, false],
				wH: 0,
				freshShow: false
			}
		},
		components: {
			tabBar,
			searchBar,
			qaList,
			rzSelectModal
		},
		computed: {
			...mapState(['infoAuthorize', 'phoneReg']),
		},
		watch: {
			current(newV) {
				let obj = this.swiperData[newV]
				if(obj.data.length>0 || obj.endFlag) return
				this.renderList()
			}
		},
		async onPullDownRefresh() {
			// console.log('down')
			this.swiperData.forEach(ele => {
				this.$set(ele, 'data', [])
				this.$set(ele, 'p', 1)
				this.$set(ele, 'endFlag', false)
			})
			// let obj = this.swiperData[this.current]
			// obj.p = 1
			// obj.endFlag = false
			// this.$set(obj, 'data', [])
			await this.renderList()
			uni.stopPullDownRefresh()
			this.$refs.uTips.show({
				title: '刷新成功',
				type: 'success',
				duration: '2000'
			})
		},
		onReachBottom() {
			let obj = this.swiperData[this.current]
			if(obj.endFlag) return
			obj.p++
			this.renderList()
			// console.log('reach')
		},
		onLoad() {
			this.renderList()
			
		},
		onShow(){
			if(uni.getStorageSync('indexRefresh')) {
				uni.startPullDownRefresh()
				uni.removeStorageSync('indexRefresh')
			}
			
		},
		onPageScroll(res) {
			res.scrollTop > 700 ? this.freshShow = true : this.freshShow = false
		},
		methods: {
			handleFresh() {
				uni.pageScrollTo({
					scrollTop: 0,
					success: () => {
						uni.startPullDownRefresh()
					}
				})
				
			},
			handleNavigator(index) {
				uni.navigateTo({
					url: this.list[index].url
				})
			},
			tabsChange(index) {
				this.current = index
				// this.renderList()
			},
			async renderList() {
				// this.loading = true
				let res
				let index = this.current
				let obj = this.swiperData[index]
				let api = '/Home/Jzbxcx/' + obj.api
				let p = obj.p
				let data = obj.data
				this.$set(obj, 'loading', true)
				res = await this.$https.get(api, {params: {p}})
				if(res.data.code == 1) {
					let resData = res.data.list.map(ele => {
						if(index == 1) {
							ele.ansNum = ele.reply_num
						}else {
							let arr = ele.reply_list.filter(el => el.zt == 2)
							ele.ansNum = Number(ele.comment_num) + arr.length
						}
						return ele
					})
					if(obj.p == 1) obj.data = []
					obj.data.push(resData)
					// this.$set(obj.data, p - 1, resData)
				}
				
				if(res.data.pages == p) {
					// obj.endFlag = true
					this.$set(obj, 'endFlag', true)
				}
				// console.log(this.endFlag)
				this.$nextTick(() => {
					// setTimeout(() => {
						this.$set(obj, 'loading', false)
					// }, 1000)
					
				})
				
				 
				
			},
			handleShowRzBox() {
				this.rzModalShow = !this.rzModalShow
			},
			handleSend() {
				
				
				if(!this.phoneReg) {
					uni.navigateTo({
						url: '/pages/wxAuthorize/wxAuthorize?phone=1'
					})
					return
				}
				if(this.infoAuthorize.auth_status == 2) {
					uni.navigateTo({
						url: '/pages/send/send?type=' + this.infoAuthorize.type,
					})
				}else {
					this.handleShowRzBox()
				}
					
				
			}
		}
	}
</script>

<style scoped lang="scss">
	.list {
		height: 100%;
	}
	.swiper-w {
		height: 100%;
		width: 100%;
	}
	.add-btn {
		position: fixed;
		right: 20rpx;
		bottom: 160rpx;
		background-color: $jzb-theme-color;
		border: 8rpx solid #fff;
		color: #fff;
		// font-weight: bold;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		width: 130rpx;
		height: 130rpx;
		font-size: 30rpx;
		border-radius: 50%;
		box-sizing: border-box;
		box-shadow: 0 0 10rpx rgba(0,0,0,0.1);
	}
	.add-btn-text {
		font-size: 24rpx;
	}
	.xx {
		color: $jzb-sup3-color;
	}
	.header {
		background-color: #fff;
		padding: 8px 15px 15px;
		margin-bottom: 15px;
	}
	.search-w {
		margin-bottom: 8px;
	}
	.content {
		background-color: #fff;
	}
</style>
